import React, {Component} from 'react';

class Count extends Component {
  constructor(props) {
    super(props);
	if(!localStorage.getItem('count['+this.props.id+']'))localStorage.setItem('count['+this.props.id+']', 0);
    this.state = { i: this.props.id, count: localStorage.getItem('count['+this.props.id+']'), courses: this.props.courses, min: 0, max: 10 };
    this.incFav = this.incFav.bind(this);
    this.decFav = this.decFav.bind(this);
    this.all = this.props.all;
	this.state.courses[this.state.i].count=this.state.count;
  }

  incFav() {
    if(this.state.count<this.state.max){
		localStorage.setItem('count['+this.state.i+']', 1+(+this.state.count));
		this.setState(prevState => ({ count: prevState.count++}));
		this.all();
	}
  }

  decFav() {
    if(this.state.count>this.state.min){
		localStorage.setItem('count['+this.state.i+']', -1+(+this.state.count));
		this.setState(prevState => ({ count: prevState.count--}));
		this.all();
	}
  }  
  handleChange(evt) {
    const count = (evt.target.validity.valid) ? evt.target.value : this.state.count;
    this.setState({ count });
	localStorage.setItem('count['+this.state.i+']', count);
		this.all();
  }
  
//{this.state.courses[this.state.i].count}

  render() {
    return(
      <div className='countbox'>
        <button className='countbox_buttonminus' onClick={this.decFav}  disabled={(this.state.count==this.state.min)?"disabled":""}>-</button>
		<input className='countbox_input' type="number" min={this.state.min} max={this.state.max} value={this.state.count} onChange={this.handleChange.bind(this)} size='2' />
        <button className='countbox_buttonplus' onClick={this.incFav} disabled={(this.state.count===this.state.max)?"disabled":""} >+</button>
      </div>
    );
  }
}

export default Count;