import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import Count from './Count.js';
import SendBar from './SendBar.js';



const courses = [
	{'id':0, 'name':'Опера лаванда', 'price':'29', 'img':'cakeclub2014_87224410_492709734743143_7170452562313570678_n', 'count':0},
	{'id':1, 'name':'Опера шоколад', 'price':'29', 'img':'cakeclub2014_85202091_813662699045919_3995615131054433788_n', 'count':0},
	{'id':2, 'name':'Захер', 'price':'25', 'img':'cakeclub2014_13388729_856046544500329_1776266064_n', 'count':0},
	{'id':3, 'name':'Эстерхази', 'price':'29', 'img':'cakeclub2014_16583952_339667163101311_786156633540526080_n', 'count':0},
	{'id':4, 'name':'Наполеон', 'price':'28', 'img':'cakeclub2014_12269940_865326096898493_419739281_n', 'count':0},
	{'id':5, 'name':'Тарт с орехами', 'price':'26', 'img':'cakeclub2014_14374441_1178092488920625_9015296440620023808_n', 'count':0},
	{'id':6, 'name':'Тыквенный пирог', 'price':'29', 'img':'cakeclub2014_119183906_236716184401620_6616880378104983101_n', 'count':0},
	{'id':7, 'name':'Чизкейк с орео', 'price':'39', 'img':'cakeclub2014_101000461_301654567665912_800298787949683864_n', 'count':0},
	{'id':8, 'name':'Красный бархат', 'price':'49', 'img':'cakeclub2014_68957633_1368987879917670_4157327316886629252_n', 'count':0}
];



class SearchBar extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      search: [],
	  summ: 0,
	  counts: 0,
	  basket: false
    }
	this.all = this.all.bind(this);
	this.baskets = this.baskets.bind(this);
  }
  
  all(){
		let count=0,
		counts=0,
		summ=0;
		courses.forEach((element, index, array) => 
			(
			count=+localStorage.getItem('count['+index+']'),
			element.count=count,
			summ+=+(element.price*count),
			counts+=+count,
			this.setState({counts: counts}),
			this.setState({summ: summ})
			)
		);
  }  
  baskets(e){
	  if(this.state.basket){if(e>0){return 'showinbasket';}else{return 'hideinbasket';}}else{return '';}
  }
  
	componentWillMount(){
		this.all();
	}

  render() {
    let options;
    if (this.state.search.length) {
      const searchPattern = new RegExp(this.state.search.map(term => `(?=.*${term})`).join(''), 'i');
      options = courses.filter(option => 
        option.name.match(searchPattern)
      );
    } else {
      options = courses;
    }

    return (
      <div id="shop">
		  <div className="top">
			<div className="search">
			  <input type="text" className={(this.state.search=='')?'':'notempty'} value={this.state.search} placeholder="Искать здесь..." onChange={(e) => this.setState({search: e.target.value.split(' ')})}/>
			  <button className={(this.state.search=='')?'':'notempty'} onClick={(e) => this.setState({search: ''})+'return false'} ></button>
			</div>
			<button onClick={(e) => this.setState({basket: !this.state.basket})+'return false'} className={'basket '+(this.state.basket ? 'back' : '')+' '+((this.state.counts>0)?'':'hide')} > </button>
		</div>
        <ul>
            {options.map((option, i) => 
              <li key={option.name+i} className={'img '+this.baskets(option.count)} style={{backgroundImage: 'url(./img/'+option.img+'.jpg)'}} ><div className='img_title' >{option.name+' '+option.price+'₴'} <Count key={option.id} id={option.id} courses={courses} all={this.all} /></div></li>
            )}
        </ul>
		<div className={'footer '+((this.state.counts>0)?'':'hide')}><SendBar courses={courses} counts={this.state.counts} summ={this.state.summ} /></div>
      </div>
    )
  }
}

ReactDOM.render(
  <React.StrictMode>
    <App />
	<SearchBar />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
